import React, {useState} from 'react'

const TodoForm = (props) => {

    const [input, setInput] = useState(props.edit ? props.edit.value : '');


    const handleChange = e => {
        setInput(e.target.value)
    }

    const handleSubmit = e => {
        e.preventDefault()

        props.onSubmit({
            id: Math.floor(Math.random() * 10000),
            text: input
        })

        setInput('')
    }

    return (
        <form className='todoForm' onSubmit={handleSubmit}>
            {props.edit
                ? (<><input type="text" placeholder='Edit your todo...' value={input} name='text'
                            className='todoInput edit'
                            onChange={handleChange} autoFocus/>
                    <button className='todoBtn edit'>Edit</button>
                </>)
                : (<><input type="text" placeholder='Add a todo...' value={input} name='text' className='todoInput'
                            onChange={handleChange} autoFocus/>
                    <button className='todoBtn'>Add</button>
                </>)}
        </form>
    )
}

export default TodoForm;