import React, {useState} from 'react'
import {RiCloseCircleLine, TiEdit} from "react-icons/all";
import TodoForm from "./TodoForm";

const Todo = ({todos, completeTodo, removeTodo, editTodo}) => {

    const [edit, setEdit] = useState({
        id: null,
        value: ''
    })

    const submitEdit = value => {
        editTodo(value, edit.id)
        setEdit({
            id: null,
            value: ''
        })
    }

    if(edit.id) {
        return <TodoForm edit={edit} onSubmit={submitEdit}/>
    }

    return todos.map((todo, id) => (
        <div className={todo.isComplete ?  'todoRow complete' : 'todoRow'} key={id}>
            <div className='todoRowText' key={todo.id} onClick={() => completeTodo(todo.id)}>
                {todo.text}
            </div>
            <div className='icons'>
                <RiCloseCircleLine onClick={() => removeTodo(todo.id)} className='deleteIcon'/>
                <TiEdit onClick={() => setEdit({id: todo.id, value: todo.text})} className='editIcon'/>
            </div>
        </div>
    ))
}

export default Todo;